function install (Http) {
  Http.mixin({
    // 请求 resolve 钩子
    $requestResolve (config) {
      // 对接CAS 使用 请和你对接的后端确认具体 参数方式 这里仅供参考
      config.headers.common['X-Requested-With'] = 'XMLHttpRequest'
      return config
    },
    // 响应成功 钩子
    $responseResolve (response) {
      return response
    },
    // 响应失败 或者 400 500 等错误
    $responseReject (error) {
      if (error.response === undefined) {
        window.rrc && window.rrc.$notify.error({
          title: '提示',
          message: '网络异常, 请稍后重试'
        })
        return Promise.reject(error)
      }
      // rrc.$notify.error('请求失败,请刷新页面重试！')
      // return Promise.reject(error)
      let {data: {data, errMsg, err_msg, message, msg}, status} = error.response // eslint-disable-line
      if (status === 401) { // 未登陆需重新登录
        const urlPrefix = JSON.parse(process.env.CLIENT).loginUrl
        const current = `?service=${encodeURIComponent(window.location.href)}`
        errMsg = '请重新登录'
        setTimeout(() => {
          window.location.href = data || (urlPrefix + current)
        }, 2000)
      }
      let msgStr = errMsg || err_msg || message || msg // eslint-disable-line
      if (msgStr) {
        window.rrc && window.rrc.$notify.error({
          title: '操作结果',
          message: msgStr
        })
        return Promise.reject(error)
      }
      return error
    },
    $message (response, isReject = false) {
      if (isReject) {
        window.rrc && window.rrc.$notify.error(response)
        return
      }
      const { data: { errMsg, err_msg, message, msg } } = response // eslint-disable-line
      let msgStr = errMsg || err_msg || message || msg // eslint-disable-line
      if (msgStr) {
        window.rrc && window.rrc.$notify.error({
          title: '操作提示',
          message: msgStr
        })
      }
    },
    $autoMessageAdapter (response) {
      const { code, status } = response.data
      if (code === '0' || status === 0 || status === '0') {
        return false
      }
      return true
    }
  })
}

export default {
  install
}
